.TodoSearch {
    background: #f9fbfc;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgb(183, 74, 216);
    border-style: dashed;
}

.TodoSearch::placeholder {
    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
    outline: invert;
}

.TodoSearch:focus {
    outline-color: #61dafa;
    box-shadow: 0px 5px 50px rgb(68, 203, 221);
}